
import {defineComponent} from "vue";
interface MenuOpen {
  isMenuOpen: boolean
}
export default defineComponent({
  name: "AppHeader",
  data(): MenuOpen {
    return {
      isMenuOpen: false
    };
  },
  methods: {
    toggleMenu() {
      this.isMenuOpen = !this.isMenuOpen;
    },
    closeMenu() {
      this.isMenuOpen  = false
    }
  }
})
